body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

.loader-text {
  color: white;
  font-size: 35px;
  position: absolute;
  top: calc(50% + 45px);
  left: calc(50% - 100px);
}
.wrap {
  width: 22em;
  height: 20em;
  position: absolute;
  top: calc(50% - 10em);
  left: calc(50% - 11em);
}
.load-wrp {
  height: 192px;
  width: 192px;
  position: absolute;
  top: calc(50% - 96px);
  left: calc(50% - 96px);
}
.load {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 128px;
  height: 128px;
  margin: 8px;
  border: 10px solid;
  border-radius: 50%;
  animation: load 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #fff transparent transparent transparent;
}
.frst {
  min-width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  z-index: -2;
  display: block;
  filter: blur(2px) brightness(0.9);
}
.greet {
  color: rgba(255, 255, 255, 1);
  z-index: -1;
  mix-blend-mode: overlay;
}
.hover {
  transition-duration: 150ms;
}
.hover:hover {
  color: #696969;
}
.hover:active {
  color: #4b4b4b;
}
@keyframes load {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.bio-text {
  text-align: center;
}
.myimg {
   display: block;
   margin: auto;
  transition-duration: 500ms;
 }
.myimg:hover {
  filter: brightness(0.75);
}
.greet {
  font-size: 3.5rem;
  font-weight: 300;
  line-height: 1.2;
}
@media (min-width: 576px) {
  .greet {
    font-size: 5.5rem;
    font-weight: 300;
    line-height: 1.2;
  }
}
@media (min-width: 768px) {

}
@media (min-width: 992px) {
  .bio-text {
    text-align: left;
  }
  .myimg {
    display: inline;
  }
}
@media (min-width: 1200px) {

}
